  height: 68px;
  display: none; }

.mainNav {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.mainNav-logo {
  margin-right: auto; }
  .mainNav-logo img {
    transition: all 0.3s ease;
    max-width: 180px;
    vertical-align: middle; }
    @media (min-width: 30em) {
      .mainNav-logo img {
        max-width: 200px; } }
    @media (min-width: 64em) {
      .mainNav-logo img {
        max-width: 100%; } }

/**
* Menu
*/
.mainNav-link {
  margin-right: 5px;
  color: #4C4C4C;
  display: none;
  padding: 4px 12px;
  cursor: pointer;
  transition: color 0.3s ease; }
  @media (min-width: 64em) {
    .mainNav-link {
      display: block; } }
  @media (min-width: 75em) {
    .mainNav-link {
      margin-right: 10px; } }
  .mainNav-link:last-of-type {
    margin-right: 0; }
  .mainNav-link:hover {
    color: #E4744E; }

.menuSelected {
  color: #E4744E; }

.mainNav-menuMobile {
  display: block;
  cursor: pointer; }
  @media (min-width: 64em) {
    .mainNav-menuMobile {
      display: none; } }

.menuText {
  display: none; }
  @media (min-width: 30em) {
    .menuText {
      display: inline-block; } }

/**
* Header Fixed
*/
.sticky {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  background: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25); }
  .sticky .dropdown {
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25); }
  .sticky .mainNav-logo img {
    max-width: 130px; }

.header-placeholder.block {
  display: block; }

/**
* Nav Mobile
*/
.mainNav-menuMobile_toggle {
  float: right;
  margin-left: 10px; }
  .mainNav-menuMobile_toggle .line {
    width: 30px;
    height: 2px;
    background-color: #A3C220;
    display: block;
    margin: 5px auto;
    transition: all 0.3s ease-in-out; }

.mainNav-menuMobile.is-active .line:nth-child(1) {
  transform: translateY(7px) rotate(45deg); }

.mainNav-menuMobile.is-active .line:nth-child(2) {
  opacity: 0; }

.mainNav-menuMobile.is-active .line:nth-child(3) {
  transform: translateY(-7px) rotate(-45deg); }

