.flags{
  background-color: $black;
  height: 100%;
  width: 100%;
  padding-top: 50px;

  header{
    margin-bottom: 50px;
    text-align: center;
  }

  article{
    padding-bottom: 20px;
    margin-bottom: 25px;
    border-bottom: 1px solid #696969;
    @include cf;

    h3{
      @include font-size(2.4,2.4);
      margin-bottom: 16px;
      color: $white;
      margin-left: 2px;
    }
  }

  ul{
    text-align: left;
    width: 50%;
    float: left;

    @include breakpoint(sm) {
      width: 33%;
    }

    @include breakpoint(md) {
      width: 20%;
    }

    li{
      margin: 15px 10px 15px 5px;
      vertical-align: top;
      line-height: 1.35em;

      a{
        color: #FFF;
        text-decoration: none;
        @include font-size(1.6, 1.6);
        display: block;
        height: 32px;
        font-weight: $medium;

        &:hover{
          text-decoration: underline;
        }

        p{
          margin-left: 42px;
        }
      }
    }
  }
}

